import React from "react";
import "./Navbar.css";

import code from "../icons/code.svg";
import certificate from "../icons/certificate.svg";
import follow from "../icons/follow.svg";

export default class Navbar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <nav>
        <a href="/"><div style={{ cursor: "pointer" }}>Alexanderbira</div></a>

        <div id="icons">
          <a href="demos">
            <img
              src={code}
              alt="code"
              draggable="false"
              onMouseDown={(e) => e.preventDefault()}
            />
          </a>
          <a href="certificates">
            <img
              src={certificate}
              alt="certificate"
              draggable="false"
              onMouseDown={(e) => e.preventDefault()}
            />
          </a>
          <a href="follow">
          <img
            src={follow}
            alt="follow"
            draggable="false"
            onMouseDown={(e) => e.preventDefault()}
          />
          </a>
        </div>
      </nav>
    );
  }
}
